import {validateEmbed, validateIsEmbed} from "./validatorUtils";

/**
 *
 */
export function validateCourseForm() {
    document.getElementById('udeh-form').addEventListener("submit", function(e) {
        e.preventDefault();
        validateIsEmbed();
        // TODO
        window.console.log('validateCourseForm');
        let validEmbed = validateEmbed('id_course_introduction_embed',
            'displayable-form-informations-container',
            'fitem_id_course_introduction_embed');
        if (!validEmbed) {
            return;
        }
        window.$('#udeh-form').submit();
    });
}
